import React, { useState } from "react";

export const Login = (props) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-blue-200">
      <form onSubmit={handleSubmit} className="max-w-xs">
        <label htmlFor="email" className="block mb-2">
          Email
        </label>
        <input
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          type="email"
          placeholder="youremail@gmail.com"
          id="email"
          name="email"
          className="border border-gray-300 rounded py-2 px-4 mb-2 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
        />
        <label htmlFor="password" className="block mb-2">
          Password
        </label>
        <input
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          type="password"
          placeholder="********"
          id="password"
          name="password"
          className="border border-gray-300 rounded py-2 px-4 mb-4 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
        />
        <button
          type="submit"
          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50"
        >
          Login
        </button>
      </form>

      <button
        onClick={() => props.onFormSwitch("signup")}
        className="text-blue-500 hover:text-blue-700 mt-4"
      >
        Don't have an account? Signup here!
      </button>
    </div>
  );
};